'use client';

import { useState } from 'react';
import {
  ScanLine,
  Package,
  ClipboardList,
  Check,
  Clock,
  Play,
  Pause,
  RotateCcw,
  ChevronRight,
  ArrowRight,
  X,
  AlertCircle,
  Move,
  UserMinus,
} from 'lucide-react';
import {
  orders,
  pickBatches as initialBatches,
  inventory,
  getProductById,
} from '@/data/mockData';
import { PickBatch, PickBatchOrder, Order } from '@/types';

export default function PickPage() {
  const [pickBatches, setPickBatches] = useState<PickBatch[]>(initialBatches);
  const [selectedBatch, setSelectedBatch] = useState<PickBatch | null>(null);
  const [activePickOrder, setActivePickOrder] = useState<PickBatchOrder | null>(
    null
  );
  const [showMoveModal, setShowMoveModal] = useState(false);
  const [orderToMove, setOrderToMove] = useState<string | null>(null);
  const [toteInput, setToteInput] = useState('');

  // Get unassigned orders (to_pick status, not in any batch)
  const assignedOrderIds = pickBatches.flatMap((b) =>
    b.orders.map((o) => o.orderId)
  );
  const unassignedOrders = orders.filter(
    (o) => o.status === 'to_pick' && !assignedOrderIds.includes(o.id)
  );

  const getOrderById = (orderId: string): Order | undefined => {
    return orders.find((o) => o.id === orderId);
  };

  const getPickItems = (batch: PickBatch) => {
    const items: Map<
      string,
      { productId: string; sku: string; name: string; quantity: number; binLocation: string }
    > = new Map();

    for (const batchOrder of batch.orders) {
      const order = getOrderById(batchOrder.orderId);
      if (!order) continue;

      for (const item of order.items) {
        if (item.productId) {
          const product = getProductById(item.productId);
          const inv = inventory.find(
            (i) => i.productId === item.productId && i.locationId === 'loc-1'
          );
          const key = item.productId;

          if (items.has(key)) {
            const existing = items.get(key)!;
            existing.quantity += item.quantity;
          } else {
            items.set(key, {
              productId: item.productId,
              sku: item.sku,
              name: item.name,
              quantity: item.quantity,
              binLocation: inv?.binLocation || 'Unknown',
            });
          }
        }
      }
    }

    return Array.from(items.values()).sort((a, b) =>
      a.binLocation.localeCompare(b.binLocation)
    );
  };

  const handleUnassignOrder = (batchId: string, orderId: string) => {
    setPickBatches((prev) =>
      prev.map((batch) => {
        if (batch.id !== batchId) return batch;
        return {
          ...batch,
          orders: batch.orders.filter((o) => o.orderId !== orderId),
        };
      })
    );
  };

  const handleMoveOrder = (
    fromBatchId: string,
    toBatchId: string,
    orderId: string
  ) => {
    const orderToMove = pickBatches
      .find((b) => b.id === fromBatchId)
      ?.orders.find((o) => o.orderId === orderId);

    if (!orderToMove) return;

    setPickBatches((prev) =>
      prev.map((batch) => {
        if (batch.id === fromBatchId) {
          return {
            ...batch,
            orders: batch.orders.filter((o) => o.orderId !== orderId),
          };
        }
        if (batch.id === toBatchId) {
          return {
            ...batch,
            orders: [
              ...batch.orders,
              { ...orderToMove, status: 'pending', toteBarcode: undefined },
            ],
          };
        }
        return batch;
      })
    );

    setShowMoveModal(false);
    setOrderToMove(null);
  };

  const handleAssignTote = (orderId: string, toteBarcode: string) => {
    if (!selectedBatch) return;

    setPickBatches((prev) =>
      prev.map((batch) => {
        if (batch.id !== selectedBatch.id) return batch;
        return {
          ...batch,
          orders: batch.orders.map((o) => {
            if (o.orderId !== orderId) return o;
            return { ...o, toteBarcode, status: 'picking' as const };
          }),
        };
      })
    );

    // Update selected batch reference
    setSelectedBatch((prev) => {
      if (!prev) return prev;
      return {
        ...prev,
        orders: prev.orders.map((o) => {
          if (o.orderId !== orderId) return o;
          return { ...o, toteBarcode, status: 'picking' as const };
        }),
      };
    });

    setToteInput('');
  };

  const handleMarkPicked = (orderId: string) => {
    if (!selectedBatch) return;

    setPickBatches((prev) =>
      prev.map((batch) => {
        if (batch.id !== selectedBatch.id) return batch;
        return {
          ...batch,
          orders: batch.orders.map((o) => {
            if (o.orderId !== orderId) return o;
            return { ...o, status: 'picked' as const };
          }),
        };
      })
    );

    setSelectedBatch((prev) => {
      if (!prev) return prev;
      return {
        ...prev,
        orders: prev.orders.map((o) => {
          if (o.orderId !== orderId) return o;
          return { ...o, status: 'picked' as const };
        }),
      };
    });

    setActivePickOrder(null);
  };

  const getStatusBadge = (status: PickBatchOrder['status']) => {
    switch (status) {
      case 'pending':
        return (
          <span className="inline-flex items-center gap-1 rounded-full bg-slate-700/50 px-2.5 py-1 text-xs font-medium text-slate-400">
            <Clock className="h-3 w-3" />
            Pending
          </span>
        );
      case 'picking':
        return (
          <span className="inline-flex items-center gap-1 rounded-full bg-blue-500/20 px-2.5 py-1 text-xs font-medium text-blue-400">
            <Play className="h-3 w-3" />
            Picking
          </span>
        );
      case 'picked':
        return (
          <span className="inline-flex items-center gap-1 rounded-full bg-emerald-500/20 px-2.5 py-1 text-xs font-medium text-emerald-400">
            <Check className="h-3 w-3" />
            Picked
          </span>
        );
    }
  };

  const getBatchStatusBadge = (status: PickBatch['status']) => {
    switch (status) {
      case 'queued':
        return (
          <span className="rounded-full bg-slate-700/50 px-2.5 py-1 text-xs font-medium text-slate-400">
            Queued
          </span>
        );
      case 'in_progress':
        return (
          <span className="rounded-full bg-blue-500/20 px-2.5 py-1 text-xs font-medium text-blue-400">
            In Progress
          </span>
        );
      case 'completed':
        return (
          <span className="rounded-full bg-emerald-500/20 px-2.5 py-1 text-xs font-medium text-emerald-400">
            Completed
          </span>
        );
    }
  };

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-white">Pick Station</h1>
          <p className="text-slate-400">
            {pickBatches.length} active batches {' '}
            {unassignedOrders.length} unassigned orders
          </p>
        </div>
        <button className="flex items-center gap-2 rounded-lg bg-emerald-500 px-4 py-2.5 text-sm font-medium text-white hover:bg-emerald-600 transition-colors">
          <ClipboardList className="h-4 w-4" />
          Create Batch
        </button>
      </div>

      <div className="grid grid-cols-1 gap-6 lg:grid-cols-3">
        {/* Batches List */}
        <div className="space-y-4">
          <h2 className="text-sm font-medium text-slate-400 uppercase tracking-wider">
            Pick Batches
          </h2>

          {pickBatches.map((batch) => {
            const pickedCount = batch.orders.filter(
              (o) => o.status === 'picked'
            ).length;
            const totalCount = batch.orders.length;
            const progress = totalCount > 0 ? (pickedCount / totalCount) * 100 : 0;

            return (
              <button
                key={batch.id}
                onClick={() => setSelectedBatch(batch)}
                className={`w-full rounded-xl p-4 text-left transition-colors ${
                  selectedBatch?.id === batch.id
                    ? 'bg-emerald-500/20 border border-emerald-500/50'
                    : 'bg-slate-800/50 border border-slate-700/50 hover:bg-slate-700/30'
                }`}
              >
                <div className="flex items-center justify-between mb-2">
                  <h3 className="text-sm font-medium text-white">{batch.name}</h3>
                  {getBatchStatusBadge(batch.status)}
                </div>
                <div className="flex items-center justify-between text-xs text-slate-400">
                  <span>{batch.orders.length} orders</span>
                  {batch.assignedTo && <span>Assigned: {batch.assignedTo}</span>}
                </div>
                {/* Progress bar */}
                <div className="mt-3 h-1.5 rounded-full bg-slate-700/50 overflow-hidden">
                  <div
                    className="h-full bg-emerald-500 transition-all"
                    style={{ width: `${progress}%` }}
                  />
                </div>
                <p className="mt-1 text-xs text-slate-500">
                  {pickedCount}/{totalCount} picked
                </p>
              </button>
            );
          })}

          {/* Unassigned Orders */}
          {unassignedOrders.length > 0 && (
            <div className="mt-6">
              <h2 className="text-sm font-medium text-slate-400 uppercase tracking-wider mb-3">
                Unassigned Orders
              </h2>
              <div className="space-y-2">
                {unassignedOrders.map((order) => (
                  <div
                    key={order.id}
                    className="rounded-lg bg-slate-800/50 border border-slate-700/50 p-3"
                  >
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-white">
                          #{order.orderNumber}
                        </p>
                        <p className="text-xs text-slate-400 flex items-center gap-1">
                          {order.items.length} items 
                          <span className={`inline-flex items-center gap-1 ${
                            order.channel === 'shopify' ? 'text-green-400' : 'text-orange-400'
                          }`}>
                            <i className={order.channel === 'shopify' ? 'fab fa-shopify' : 'fab fa-amazon'}></i>
                            {order.channel === 'shopify' ? 'Shopify' : 'Amazon'}
                          </span>
                        </p>
                      </div>
                      <button className="text-xs text-emerald-400 hover:text-emerald-300">
                        Assign
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>

        {/* Batch Details */}
        <div className="lg:col-span-2 space-y-6">
          {selectedBatch ? (
            <>
              {/* Batch Header */}
              <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-6">
                <div className="flex items-center justify-between mb-4">
                  <div>
                    <h2 className="text-lg font-semibold text-white">
                      {selectedBatch.name}
                    </h2>
                    <p className="text-sm text-slate-400">
                      {selectedBatch.orders.length} orders in batch
                    </p>
                  </div>
                  {getBatchStatusBadge(selectedBatch.status)}
                </div>

                {/* Consolidated Pick List */}
                <div className="mt-4">
                  <h3 className="text-sm font-medium text-slate-400 mb-3">
                    Consolidated Pick List
                  </h3>
                  <div className="rounded-lg bg-slate-700/30 overflow-hidden">
                    <table className="w-full">
                      <thead>
                        <tr className="border-b border-slate-600/30">
                          <th className="px-4 py-2 text-left text-xs font-medium text-slate-400">
                            Bin
                          </th>
                          <th className="px-4 py-2 text-left text-xs font-medium text-slate-400">
                            Item
                          </th>
                          <th className="px-4 py-2 text-right text-xs font-medium text-slate-400">
                            Qty
                          </th>
                        </tr>
                      </thead>
                      <tbody className="divide-y divide-slate-600/30">
                        {getPickItems(selectedBatch).map((item) => (
                          <tr key={item.productId}>
                            <td className="px-4 py-2">
                              <span className="rounded bg-emerald-500/20 px-2 py-0.5 text-xs font-mono text-emerald-400">
                                {item.binLocation}
                              </span>
                            </td>
                            <td className="px-4 py-2">
                              <p className="text-sm text-white">{item.name}</p>
                              <p className="text-xs text-slate-400">{item.sku}</p>
                            </td>
                            <td className="px-4 py-2 text-right">
                              <span className="text-sm font-medium text-white">
                                {item.quantity}
                              </span>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>

              {/* Orders in Batch */}
              <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-6">
                <h3 className="text-md font-semibold text-white mb-4">
                  Orders in Batch
                </h3>
                <div className="space-y-3">
                  {selectedBatch.orders.map((batchOrder) => {
                    const order = getOrderById(batchOrder.orderId);
                    if (!order) return null;

                    return (
                      <div
                        key={batchOrder.orderId}
                        className={`rounded-lg p-4 transition-colors ${
                          activePickOrder?.orderId === batchOrder.orderId
                            ? 'bg-blue-500/20 border border-blue-500/50'
                            : 'bg-slate-700/30 border border-slate-600/30'
                        }`}
                      >
                        <div className="flex items-center justify-between">
                          <div className="flex items-center gap-4">
                            <div>
                              <p className="text-sm font-medium text-white">
                                Order #{order.orderNumber}
                              </p>
                              <p className="text-xs text-slate-400">
                                {order.customer.name}  {order.items.length} items
                              </p>
                              {batchOrder.toteBarcode && (
                                <p className="text-xs text-emerald-400 mt-1">
                                  Tote: {batchOrder.toteBarcode}
                                </p>
                              )}
                            </div>
                          </div>
                          <div className="flex items-center gap-3">
                            {getStatusBadge(batchOrder.status)}

                            {/* Action Buttons */}
                            <div className="flex items-center gap-1">
                              {batchOrder.status === 'pending' && (
                                <>
                                  <button
                                    onClick={() => {
                                      setActivePickOrder(batchOrder);
                                    }}
                                    className="rounded p-1.5 text-slate-400 hover:text-white hover:bg-slate-700/50 transition-colors"
                                    title="Start picking"
                                  >
                                    <Play className="h-4 w-4" />
                                  </button>
                                </>
                              )}
                              {batchOrder.status === 'picking' && (
                                <button
                                  onClick={() =>
                                    handleMarkPicked(batchOrder.orderId)
                                  }
                                  className="rounded p-1.5 text-emerald-400 hover:text-emerald-300 hover:bg-emerald-500/20 transition-colors"
                                  title="Mark as picked"
                                >
                                  <Check className="h-4 w-4" />
                                </button>
                              )}

                              {/* Move button */}
                              <button
                                onClick={() => {
                                  setOrderToMove(batchOrder.orderId);
                                  setShowMoveModal(true);
                                }}
                                className="rounded p-1.5 text-slate-400 hover:text-white hover:bg-slate-700/50 transition-colors"
                                title="Move to another batch"
                              >
                                <Move className="h-4 w-4" />
                              </button>

                              {/* Unassign button */}
                              <button
                                onClick={() =>
                                  handleUnassignOrder(
                                    selectedBatch.id,
                                    batchOrder.orderId
                                  )
                                }
                                className="rounded p-1.5 text-slate-400 hover:text-red-400 hover:bg-red-500/20 transition-colors"
                                title="Unassign from batch"
                              >
                                <UserMinus className="h-4 w-4" />
                              </button>
                            </div>
                          </div>
                        </div>

                        {/* Tote Assignment */}
                        {activePickOrder?.orderId === batchOrder.orderId &&
                          !batchOrder.toteBarcode && (
                            <div className="mt-4 pt-4 border-t border-slate-600/30">
                              <div className="flex items-center gap-3">
                                <ScanLine className="h-5 w-5 text-blue-400" />
                                <input
                                  type="text"
                                  value={toteInput}
                                  onChange={(e) => setToteInput(e.target.value)}
                                  onKeyDown={(e) => {
                                    if (e.key === 'Enter' && toteInput) {
                                      handleAssignTote(
                                        batchOrder.orderId,
                                        toteInput
                                      );
                                    }
                                  }}
                                  placeholder="Scan tote barcode..."
                                  className="flex-1 rounded-lg bg-slate-700/50 border border-slate-600/50 py-2 px-3 text-sm text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500/50"
                                  autoFocus
                                />
                                <button
                                  onClick={() => {
                                    if (toteInput) {
                                      handleAssignTote(
                                        batchOrder.orderId,
                                        toteInput
                                      );
                                    }
                                  }}
                                  className="rounded-lg bg-blue-500 px-4 py-2 text-sm font-medium text-white hover:bg-blue-600 transition-colors"
                                >
                                  Assign
                                </button>
                              </div>
                            </div>
                          )}

                        {/* Pick Items for this order */}
                        {activePickOrder?.orderId === batchOrder.orderId &&
                          batchOrder.toteBarcode && (
                            <div className="mt-4 pt-4 border-t border-slate-600/30">
                              <p className="text-xs text-slate-400 mb-2">
                                Items to pick:
                              </p>
                              <div className="space-y-2">
                                {order.items.map((item) => {
                                  const inv = inventory.find(
                                    (i) =>
                                      i.productId === item.productId &&
                                      i.locationId === 'loc-1'
                                  );
                                  return (
                                    <div
                                      key={item.id}
                                      className="flex items-center justify-between rounded bg-slate-800/50 p-2"
                                    >
                                      <div className="flex items-center gap-3">
                                        <span className="rounded bg-emerald-500/20 px-2 py-0.5 text-xs font-mono text-emerald-400">
                                          {inv?.binLocation || '?'}
                                        </span>
                                        <div>
                                          <p className="text-sm text-white">
                                            {item.name}
                                          </p>
                                          <p className="text-xs text-slate-400">
                                            {item.sku}
                                          </p>
                                        </div>
                                      </div>
                                      <span className="text-sm font-medium text-white">
                                        {item.quantity}
                                      </span>
                                    </div>
                                  );
                                })}
                              </div>
                              <button
                                onClick={() =>
                                  handleMarkPicked(batchOrder.orderId)
                                }
                                className="mt-3 w-full rounded-lg bg-emerald-500 py-2 text-sm font-medium text-white hover:bg-emerald-600 transition-colors"
                              >
                                <Check className="inline h-4 w-4 mr-1" />
                                Complete Pick
                              </button>
                            </div>
                          )}
                      </div>
                    );
                  })}
                </div>
              </div>
            </>
          ) : (
            <div className="rounded-xl bg-slate-800/50 backdrop-blur border border-slate-700/50 p-12 text-center">
              <ClipboardList className="mx-auto h-16 w-16 text-slate-600" />
              <h3 className="mt-4 text-lg font-medium text-white">
                Select a Batch
              </h3>
              <p className="mt-2 text-sm text-slate-400">
                Choose a pick batch from the list to view orders and start
                picking
              </p>
            </div>
          )}
        </div>
      </div>

      {/* Move Order Modal */}
      {showMoveModal && orderToMove && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm">
          <div className="w-full max-w-md rounded-xl bg-slate-800 border border-slate-700 p-6 shadow-xl">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-white">
                Move Order to Batch
              </h3>
              <button
                onClick={() => {
                  setShowMoveModal(false);
                  setOrderToMove(null);
                }}
                className="text-slate-400 hover:text-white"
              >
                <X className="h-5 w-5" />
              </button>
            </div>

            <p className="text-sm text-slate-400 mb-4">
              Select destination batch for order #
              {getOrderById(orderToMove)?.orderNumber}
            </p>

            <div className="space-y-2">
              {pickBatches
                .filter((b) => b.id !== selectedBatch?.id)
                .map((batch) => (
                  <button
                    key={batch.id}
                    onClick={() => {
                      if (selectedBatch) {
                        handleMoveOrder(selectedBatch.id, batch.id, orderToMove);
                      }
                    }}
                    className="w-full flex items-center justify-between rounded-lg bg-slate-700/30 p-3 text-left hover:bg-slate-700/50 transition-colors"
                  >
                    <div>
                      <p className="text-sm font-medium text-white">
                        {batch.name}
                      </p>
                      <p className="text-xs text-slate-400">
                        {batch.orders.length} orders
                      </p>
                    </div>
                    <ArrowRight className="h-4 w-4 text-slate-400" />
                  </button>
                ))}
            </div>

            <div className="mt-4 pt-4 border-t border-slate-700/50">
              <button
                onClick={() => {
                  if (selectedBatch) {
                    handleUnassignOrder(selectedBatch.id, orderToMove);
                    setShowMoveModal(false);
                    setOrderToMove(null);
                  }
                }}
                className="w-full flex items-center justify-center gap-2 rounded-lg bg-red-500/20 py-2 text-sm font-medium text-red-400 hover:bg-red-500/30 transition-colors"
              >
                <UserMinus className="h-4 w-4" />
                Unassign from All Batches
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
